import Future from 'promise-future'
import qr from 'qr-image'

function readAll(readable) {
  const result = new Future()

  let buffer
  readable.on('readable', () => {
    const next = readable.read()
    buffer = buffer ? Buffer.concat([buffer, next]) : next
  })
  readable.on('end', () => {
    result.resolve(buffer)
  })

  return result.promise
}

function* qrCodeSvg(input) {
  const code = qr.image(input, {type: 'svg'})
  const buffer = yield readAll(code)
  return buffer.toString('utf8')
}

function pad(value, size = 2, char = '0') {
  let str = value.toString()
  while (str.length < size) {
    str = char + str
  }
  return str
}

function formatTime(time) {
  if (!time) {
    return ''
  }

  let rest = Math.trunc(time / 1000)
  const secs = rest % 60
  rest = Math.trunc(rest / 60)
  const minutes = rest % 60
  rest = Math.trunc(rest / 60)
  const hours = rest % 24

  return `${pad(hours)}:${pad(minutes)}:${pad(secs)}`
}

function sleep(ms) {
  return new Promise((resolve) => {
    setTimeout(resolve, ms)
  })
}

export {
  readAll,
  qrCodeSvg,
  pad,
  formatTime,
  sleep,
}



