import {
  UPDATE_CURRENT_GAME,
  UPDATE_QR_CODE,
  UPDATE_TIME,
} from '../actions/const'

const initialState = {}

function reducer(state = initialState, action) {
  /* Keep the reducer clean - do not mutate the original state. */
  let timeLeft

  switch (action.type) {
    case UPDATE_CURRENT_GAME:
      {
        const game = action.game
        return Object.assign(state, game)
      }
    case UPDATE_QR_CODE:
      {
        const payCode = action.base64
        return Object.assign({}, state, {payCode})
      }
    case UPDATE_TIME:
      {
        if (state.timeToFinish !== undefined) {
          timeLeft = state.timeToFinish
        } else if (state.deadline !== undefined) {
          timeLeft = state.deadline - new Date()
        }
        if (timeLeft < 0) {
          timeLeft = 0
        }
        return Object.assign({}, state, {timeLeft})
      }
    default:
      return state
  }
}

module.exports = reducer



