/* eslint react/no-danger: "off" */

import React from 'react'
import co from 'co'
import MaxwinClient from 'maxwin_client'
import Future from 'promise-future'
import big from 'big.js'
import './app.sass'
import {qrCodeSvg, formatTime, sleep} from '../utils'
import Loader from './Loader'
import propTypes from '../types/app'
import Converter from '../service/converter'

class AppComponent extends React.Component {

  componentDidMount() {
    this.api = new MaxwinClient.GamesApi()
    this.converter = new Converter()
    this.actions = this.props.actions
    co(this.startCycle())
  }

  getPaymentUri() {
    const {currentGame} = this.props
    return `bitcoin:${currentGame.address}?amount=${currentGame.nextBet}&label=Iceberg`
  }

  * getCurrentGame() {
    let games = []
    try {
      games = yield Future.call(this.api.getCurrentGames.bind(this.api))
      this.actions.updateNetworkState(true)
    } catch (e) {
      this.actions.updateNetworkState(false)
    }
    const game = games[0]

    if (game) {
      game.nextBet = this.calcNextBet(game)
      try {
        game.balanceFiat = (yield this.converter.convert(game.balance, 'USD')).toString()
        game.nextBetFiat = (yield this.converter.convert(game.nextBet, 'USD')).toString()
      } catch (e) {
        delete game.balanceFiat
        delete game.nextBetFiat
      }
    }

    return game
  }

  calcNextBet(game) {
    let nextBet
    const maxBet = big(game.maxAmount || 0)
    if (maxBet.gt(0)) {
      nextBet = maxBet.plus('0.00000001').toString()
    } else {
      nextBet = big('0.002').toString()
    }
    return nextBet
  }

  * startCycle() {
    yield [
      this.updateGame(),
      this.updateTime(),
    ]
  }

  * updateGame() {
    this.actions.updateCurrentGame(yield this.getCurrentGame())
    const code = yield qrCodeSvg(this.getPaymentUri())
    this.actions.updateQrCode(code)

    yield sleep(3000)
    yield this.updateGame()
  }

  * updateTime() {
    this.actions.updateTime()

    yield sleep(500)
    yield this.updateTime()
  }

  renderFiat(amount) {
    return amount === undefined ? null : (
      <span>({amount}$)</span>
    )
  }

  render() {
    const {
      currentGame,
      appState,
      } = this.props
    return currentGame.maxAmount === undefined ?
      null : (
        <div className={appState.networkOk ? null : 'modal-open'}>
          <header>
            <section>
              <div>&nbsp;</div>
              <div>Iceberg<span>first fair bitcoin lottery</span></div>
              <div>&nbsp;</div>
            </section>
          </header>
          <section>
            <div>&nbsp;</div>
            <div className="index">
              <div>
                <div>Current game: <a
                  href={`https://blockchain.info/address/${currentGame.address}`}>{currentGame.address}</a></div>
              </div>
              <div className="qrBar">
                <div className="qrCode" dangerouslySetInnerHTML={{__html: currentGame.payCode}}/>
                <div className="qrInfo">
                  <div><a href={this.getPaymentUri()}>
                    Pay {currentGame.nextBet} BTC {this.renderFiat(currentGame.nextBetFiat)}
                  </a></div>
                  <div>to win</div>
                  <div>{currentGame.balance} BTC {this.renderFiat(currentGame.balanceFiat)}</div>
                  <div>{formatTime(currentGame.timeLeft)}</div>
                </div>
              </div>
            </div>
            <div>&nbsp;</div>
          </section>
          <Loader/>
        </div>
    )
  }
}

AppComponent.propTypes = propTypes

AppComponent.defaultProps = {
}

export default AppComponent



