import request from 'superagent'
import Future from 'promise-future'
import big from 'big.js'
import {sleep} from '../utils'

const apiUrl = 'https://cors-anywhere.herokuapp.com/https://blockchain.info/ticker'
const waitTime = 2000
const expireTime = 60 // seconds

class Converter {

  * convert(amount, currency) {
    const quotes = yield this.loadQuotesCaching()
    const quote = quotes[currency]
    if (quote === undefined) {
      throw new Error('unknown currency')
    }
    return big(amount).mul(quote).round(2, 1)
  }

  * loadQuotesCaching() {
    if (!this.quotes || new Date() - this.timestamp > expireTime * 1000) {
      this.timestamp = new Date()
      this.quotes = yield this.loadQuotes()
    }
    return this.quotes
  }

  * loadQuotes() {
    try {
      return yield this.loadQuotesOnce()
    } catch (e) {
      yield sleep(waitTime)
      return yield this.loadQuotes()
    }
  }

  * loadQuotesOnce() {
    const req = request.get(apiUrl)
    const resp = yield Future.call(req.end.bind(req))
    const text = resp.text
    const replaced = text.replace(/(\d+\.?\d*)(,|})/g, '"$1"$2') // convert to string to don't loose precision
    const quotes = JSON.parse(replaced)
    Object.keys(quotes).forEach((key) => {
      const last = quotes[key].last
      quotes[key] = last
    })
    return quotes
  }

}

export default Converter



